<script>
import { archiveFileList, allProjectOrFund, archiveDelFile, getTags, projectOrFundDir, archiveUploadFile, archiveDownFile, dirTree, mkProjOrFundDir, delProjOrFundDir, editProjOrFundDir, moveFile, unArchiveFile } from '../../config/api'
import { dialogConfirm, dialogConfirmHasbtn, getFileTypeMutil } from '../../config/mUtils'
export default {
  data() {
    return {
      pagetype: '', //1=>从其他地方过来 2=>从项目管理进入
      pagefrom:'',
      type: 1, //1项目 2基金 3.待归档
      typename: '项目', //1项目 2基金
      ammend: 0, //0=>默认文件夹 1=>非默认文件夹
      level: 0, //当前文件夹层级0=>第一层
      //面包屑
      breadList: [{
        level: -1,
        name: '档案库',
        id: 1
      }],
      total: 0, //全部文件数
      projectList: [], //项目列表
      projectListVal: '', //项目列表下拉框绑定数据
      projectListValId: '', //项目列表下拉框选中id（-1=>未选项目）

      //////////////////////////////////////////////////左侧文件类型列表
      filetypeList: [],


      fileList: [], //文件列表
      isloading: false, //是否正在加载
      currentPage: 1,

      hasmoredata: true, //是否有更多数据
      hasnodata: true, //是否有数据

      //获取目录时所需数据
      paramList: {
        dirId: '', //目录id
        dirName: '', //目录id
        search: '',
        mark: '', //左侧已选中类型
        number: 10,
        page: 1
      },

      //多选已选中的文件id
      checkedAll: false,
      selectedFileIdList: [],

      //////////////////////////////////移动文件弹窗
      movefileDialog: false,
      shareProp: {
        children: 'contents'
      },
      //选择目标文书（项目/基金
      movefileProOrFund: {
        projectList: [{
          id: 1,
          name: '项目'
        }, {
          id: 2,
          name: '基金'
        }, ], //项目列表
        projectListVal: 1, //项目列表
      },
      //选择目标项目
      movefileProject: {
        projectList: [], //项目列表
        projectListVal: '', //项目列表
      },
      fileTree: [], //当前文件树
      selectMoveDoc: '', //选择要移动的文件id
      selectMoveFolder: '', //选择要移动到的文件夹id

      /////////////////////////////////////新建文件夹弹窗
      createFolderDialog: false,
      createFolderDialogTitle: '新建文件夹',
      createFolderDialogType: 0, //0=>新建1=>修改
      createFolderDialogId: 0, //当前修改的文件夹id
      newFolderName: '',




      /////////////////////////////////上传文件弹窗
      fileType: {
        value: '',
        filetypeList: [],
        placeholder: '请选择'
      },
      fileuploadDialog: false,
      //上传文件所需参数，当前页面只需文件列表及标签列表相关
      uploadfile: {
        projectList: '',
        projectVal: '',
        projectId: '',
        folderList: '',
        folderListVal: '',
        folderListId: '',
        fileList: [],
        tagList: '',
        tagListVal: '',
        tagListId: '',
      },
    }
  },
  components: {},
  watch: {
    $route(to, from) {
      this.init()
    }
  },
  computed: {
    //项目总数
    projecttotal: function() {
      if (this.type != 3) {
        if ((this.projectList.length - 1) < 0) {
          return 0
        } else {
          return this.projectList.length - 1
        }
      } else {
        return this.fileList.length
      }

    },
    //弹出框验证
    projectIdError: function() {
      if (this.uploadfile.projectVal == '') {
        return true
      } else {
        return false
      }
    },
    folderIdError: function() {
      if (this.uploadfile.folderListVal == '') {
        return true
      } else {
        return false
      }
    },
    fileListError: function() {
      let count = 0;
      if (this.uploadfile.fileList.length > 0) {
        this.uploadfile.fileList.forEach(item => {
          if (!item.typeCheck) {
            count++;
          }
        })
      }
      if (count > 0) {
        return '暂不支持视频和音频文件'
      } else {
        return ''
      }
    },
    tagListError: function() {
      if (this.uploadfile.tagListVal == '') {
        return false
      } else {
        return false
      }
    },
    submitOk: function() {
      if (this.type == 1) {
        if (this.fileListError == '' && !this.tagListError && this.uploadfile.fileList.length > 0) {
          return true;
        } else {
          return false
        }
      } else {
        if (this.fileListError == '' && this.uploadfile.fileList.length > 0) {
          return true;
        } else {
          return false
        }
      }
    },

  },
  created() {
    this.init()
  },
  methods: {
    init() {
      let routename = this.$route.name;
      if (routename == 'investDocumentManage') {
        this.type = 1;
        this.typename = '项目';
      } else if (routename == 'fundDocumentManage') {
        this.type = 2;
        this.typename = '基金';
      } else if (routename == 'archiveDocumentManage') {
        this.type = 3;
        this.typename = '待归档';
      }
      if (this.$route.query.from != undefined && this.$route.query.from != '') {
        this.pagetype = 2;
        this.pagefrom=this.$route.query.from;
      } else {
        this.pagetype = 1;
        this.pagefrom=1;
      }
      if (this.$route.query.folderId != undefined && this.$route.query.projectId != undefined && this.$route.query.folderName != undefined) {
        let currentbread = {
          level: 0,
          name: this.$route.query.folderName,
          id: this.$route.query.folderId
        }
        this.breadList.push(currentbread);
        this.paramList.dirId = this.$route.query.folderId;
        this.paramList.id = this.$route.query.projectId;
        this.projectListValId = this.$route.query.projectId;
        this.paramList.dirName = this.$route.query.folderName;
        this.ammend = this.$route.query.ammend;
      } else {

      };
      this.initData();
      this.getFolderTree();
      this.getProjectList();
    },
    //刷新数据
    initData(refresh) {
      this.isloading = true;
      if (refresh != false) {
        this.fileList = [];
        this.currentPage = 1;
        this.checkedAll = false;
        this.hasmoredata = true;
        this.selectedFileIdList = [];
      }
      let id = '';
      if (this.projectListValId != -1 && this.projectListValId != -3) {
        id = this.projectListValId
      }
      let fileliststring = [];
      //获取文件列表
      if (this.type != 3) {
        archiveFileList({
          type: this.type, //1项目
          id: id,
          dirId: this.paramList.dirId, //目录id
          search: this.paramList.search,
          markId: this.paramList.mark,
          number: this.paramList.number,
          page: this.currentPage
        }).then(res => {
          if (res.data.msgNo == 1) {
            this.filetypeList = res.data.payload.menu;
            fileliststring = res.data.payload.data;
            this.initDataStep2(fileliststring);
          }
        })
      } else {
        unArchiveFile({
          number: this.paramList.number,
          page: this.currentPage
        }).then(res => {
          if (res.data.msgNo == 1) {
            fileliststring = res.data.payload;
            this.initDataStep2(fileliststring);
          }
        })
      }

    },
    initDataStep2(fileliststring) {
      //文件列表处理
      let table_token =  window.localStorage.getItem('htcx_server_table_token') ;
      fileliststring.forEach(item => {
        item.url = item.url+'&table_token='+table_token;
        item.checked = false;
        item.add_time = new Date(item.add_time * 1000).Format('yyyy-MM-dd hh:mm:ss')
        if (item.time == null) {
          item.time = item.add_time;
        }
        //item.time = new Date(item.time * 1000).Format('yyyy-MM-dd hh:mm')
        if (item.fileSize == '') {
          item.fileSize = '--'
        }
        //文件格式转换
        let typelist = ['pdf', 'txt', 'ppt', 'xlsx', 'doc', 'docx', 'pic', 'zip', 'txt', 'rar', 'mp3'];
        let typestring = '';
        if (item.doc_title != undefined) {
          typestring = this.getFileTypeString(item.doc_title);
        }
        if (typelist.indexOf(typestring) == -1) {
          if (item.mark_id == 1) {
            item.fileType = 'word'
          } else if (item.mark_id == 2) {
            item.fileType = 'xls'
          } else if (item.mark_id == 3) {
            item.fileType = 'ppt'
          } else if (item.mark_id == 4) {
            item.fileType = 'pdf'
          } else if (item.mark_id == 5) {
            item.fileType = 'pic'
          } else {
            item.fileType = 'unno'
          }
        } else {
          item.fileType = typestring
        }
        //文档类格式
      })
      //文件类型列表
      let totalcount = 0;
      this.filetypeList.forEach(item => {
        if (item.count == null) {
          item.count = 0;
        }
        totalcount += item.count;
      })
      this.filetypeList.unshift({
        mark_id: '',
        descrip: '全部文件',
        count: totalcount
      })
      this.total = totalcount;
      if (fileliststring.length < this.paramList.number) {
        this.hasmoredata = false;
      } else {
        this.hasmoredata = true;
      }
      this.fileList = this.fileList.concat(fileliststring);
      this.isloading = false;
    },
    viewfile(originUrl, url, title) {
      let filetype = this.getFileTypeString(title);
      let canopenlist = ["jpg", "jpeg", "gif", "png", "bmp", "Webp", "rm", "rmvb", "mp4", "mov", "mtv", "wmv", "avi", "3gp", "flv", "pdf", "doc", "docx", "ppt", "pptx", "xls", "xlsx", "txt"]
      if (canopenlist.indexOf(filetype) != -1) {
        let query = {
          filetype: filetype,
          url: originUrl
        }
        if (filetype == 'pdf') {
          query.url = url
        }
        const { href } = this.$router.resolve({
          name: 'documentView',
          query: query
        })
        window.open(href, '_blank')
      } else {
        dialogConfirm('info', '暂不支持打开该文件')
      }

    },
    //文件夹操作--------------------------------------------
    //删除文件夹
    deleteFileFunc(id, count) {
      dialogConfirmHasbtn('确定删除吗？').then(() => {
        delProjOrFundDir({
          type: this.type,
          dirId: id,
          count: count
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '文件删除成功');
            this.initData();
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      }).catch(() => {});

    },
    //修改文件夹
    editFileFunc(id, name) {
      this.createFolderDialog = true;
      this.newFolderName = name;
      this.createFolderDialogTitle = '修改文件夹';
      this.createFolderDialogType = 1;
      this.createFolderDialogId = id;

    },
    //新建文件夹弹窗------------------------------------------
    createFolder() {
      if (this.projectListVal == '' || this.projectListVal == -1) {
        dialogConfirm('info', '请选择' + this.typename)
      } else {
        this.createFolderDialog = true;
        this.createFolderDialogTitle = '新建文件夹';
        this.newFolderName = '';
        this.createFolderDialogType = 0;
      }
    },
    //提交新建文件夹
    createFolderDialogClick() {
      if (this.newFolderName == '') {
        dialogConfirm('info', '文件夹名称不能为空')
      } else {
        if (this.createFolderDialogType == 0) {
          mkProjOrFundDir({
            type: this.type,
            dirname: this.newFolderName,
            id: this.projectListValId,
            pid: this.paramList.dirId,
          }).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '创建成功');
              this.createFolderDialog = false;
              this.initData();
            } else {
              dialogConfirm('fail', res.data.message);
            }
          })
        } else {
          editProjOrFundDir({
            type: this.type,
            dirname: this.newFolderName,
            dirId: this.createFolderDialogId
          }).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '文件夹名称修改成功');
              this.createFolderDialog = false;
              this.initData();
            } else {
              dialogConfirm('fail', res.data.message);
            }
          })
        }

      }
    },
    //获取项目列表
    getProjectList() {
      let currenttype = this.type;
      if (this.type == 3) {
        currenttype = this.movefileProOrFund.projectListVal;
      }
      //获取项目列表
      allProjectOrFund({
        type: currenttype
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.projectList = JSON.parse(JSON.stringify(res.data.payload.data));
          this.movefileProject.projectList = JSON.parse(JSON.stringify(res.data.payload.data));
          this.movefileProject.projectListVal = '';
          if (this.projectList.length != 0) {
            this.projectList.unshift({
              id: -1,
              name: '全部'
            })
          }
          if (this.projectListValId == -1 || this.projectListValId == -3) {
            this.projectListVal = '';
          } else {
            this.projectList.forEach(item => {
              if (this.projectListValId == item.id) {
                this.projectListVal = item.id;
              }
            })
          }
        }
      });
    },
    //下拉加载更多
    getListData() {
      if (this.hasmoredata) {
        this.currentPage++;
        this.isloading = true;
        this.initData(false)
      }
    },
    //修改文件类型筛选
    changeMark(mark) {
      this.paramList.mark = mark;
      this.initData();
    },
    //修改项目
    changeProjectId(e) {
      this.projectListValId = e;
      this.initData();
    },
    //修改搜索字段
    changeSearch() {
      this.initData();
    },

    //选择一个文件
    selectOne(index) {
      if (this.fileList[index].checked) {
        this.selectedFileIdList.push(this.fileList[index].id);
      } else {
        this.selectedFileIdList.splice(this.selectedFileIdList.indexOf(this.fileList[index].id), 1);
      }
      if (this.selectedFileIdList.length != this.fileList.length) {
        this.checkedAll = false
      } else {
        this.checkedAll = true
      }
    },
    selectAll() {
      if (this.checkedAll) {
        let idlist = [];
        this.fileList.forEach(item => {
          if (item.dirname == undefined) {
            item.checked = true;
            idlist.push(item.id);
          }
        })
        this.selectedFileIdList = idlist;
      } else {
        this.fileList.forEach(item => {
          item.checked = false;
        })
        this.selectedFileIdList = []
      }
    },
    //单个删除文件
    deleteoneFile(id) {
      dialogConfirmHasbtn('确认要删除吗？').then(res => {
        let list = [];
        list.push(id);
        archiveDelFile({
          idStr: list.toString()
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '删除成功！');
            this.initData();
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      }).catch(() => {})

    },
    //单个下载文件
    /*downloadoneFile(index) {
      archiveDownFile({
        name:this.fileList[index].doc_title,
        url:this.fileList[index].url
      }).then(res=>{
        this.download(res.data)
      })
    },*/
    //批量删除文件
    deleteMoreFile() {
      if (this.selectedFileIdList.length == 0) {
        dialogConfirm('info', '请选择需要删除的文件～')
      } else {
        dialogConfirmHasbtn('确认要删除吗？').then(res => {
          archiveDelFile({
            idStr: this.selectedFileIdList.toString()
          }).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '删除成功！');
            } else {
              dialogConfirm('fail', res.data.message);
            }
            this.initData();
          })
        }).catch(() => {})

      }
    },
    //返回文件夹
    gobackFloder(item) {
      if (item.level == -1) {
        let routername = '';
        if (this.type == 1) {
          routername = 'directInvestProjectRunProjectFile'
        } else {
          routername = 'fundFolderManage'
        }
        let param = {
          projectId: this.projectListValId,
          from:this.pagefrom
        }
        if (this.$route.query.pname != undefined) {
          param.cName = this.$route.query.pname;
        }
        this.$router.push({
          name: routername,
          query: param
        })
      } else {
        let newbread = [];
        this.breadList.forEach(i => {
          if (i.level <= item.level) {
            newbread.push(i);
          }
        })
        this.breadList = newbread;
        this.paramList.dirId = item.id;
        this.level = item.level;
        this.initData();
      }
    },
    //前往新的文件夹内部
    gotofolderDetail(projectid, dirname, dirid) {
      this.paramList.dirId = dirid;
      if (projectid != 0) {
        this.projectListValId = projectid;
        this.projectListVal = projectid;
      }
      this.level++;
      let currentbread = {
        level: this.level,
        name: dirname,
        id: dirid
      }
      this.breadList.push(currentbread);
      this.initData();
    },
    //移动文件弹窗------------------------------------------------------------------------------
    removeFile(id) {
      if (id == -1) {
        if (this.selectedFileIdList.length == 0) {
          dialogConfirm('info', '您还没有选择要移动的文件');
        } else {
          this.selectMoveDoc = this.selectedFileIdList;
          this.movefileDialog = true;
        }
      } else {
        this.selectMoveDoc = id; //选择要移动的文件id
        this.movefileDialog = true;
      }
      this.selectMoveFolder = ''; //选择要移动到的文件夹id  
      this.movefileProject.projectListVal = '';
      this.getFolderTree();
    },
    //获取选中节点
    getcheckednode(node, data) {
      this.selectMoveFolder = node
    },
    //修改项目
    changeMovefileProjectId() {
      this.getFolderTree();
    },
    //修改移动到项目/基金
    changeMovefileProOrFund() {
      this.getProjectList();
      this.getFolderTree();
    },
    //获取文件树
    getFolderTree() {
      let param = {
        type: this.type,
        id: this.movefileProject.projectListVal,
        flag: 2
      }
      if (this.type == 3) {
        param.type = this.movefileProOrFund.projectListVal
      }
      dirTree(param).then(res => {
        if (res.data.msgNo == 1) {
          this.fileTree = res.data.payload
        }
      })
    },
    //移动
    removeFileSubmit() {
      if (this.selectMoveFolder == '') {
        dialogConfirm('info', '您还没有选择目标文件夹');
      } else {
        moveFile({
          type: this.type == 3 ? this.movefileProOrFund.projectListVal : this.type,
          fileIdStr: this.selectMoveDoc.toString(),
          newDirId: this.selectMoveFolder.id,
          companyId: this.movefileProject.projectListVal
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '移动成功');
            this.movefileDialog = false;
            this.initData();
          } else {
            dialogConfirm('fail', res.data.message)
          }
        })
      }

    },
    /*------------------------------------弹窗---------------------------------------------*/
    //获取项目列表
    getModalProjectList() {
      //获取项目列表
      allProjectOrFund({
        type: this.type
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.uploadfile.projectList = res.data.payload.data;
          if (this.projectListValId == -1) {
            this.uploadfile.projectVal = '';
            this.uploadfile.projectId = this.uploadfile.projectList[0].id;
          } else {
            this.uploadfile.projectId = this.projectListValId;
            this.uploadfile.projectList.forEach(item => {
              if (item.id == this.uploadfile.projectId) {
                this.uploadfile.projectVal = item.name;
              }
            })
          }
          this.getModalFolderList();
        }
      });
    },
    getModalFolderList() {
      projectOrFundDir({
        type: this.type,
        id: this.uploadfile.projectId
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.uploadfile.folderList = res.data.payload;
          if (this.projectListValId == -1) {
            this.uploadfile.folderListVal = '';
            this.uploadfile.folderListId = this.uploadfile.folderList[0].id;
          } else {
            this.uploadfile.folderListVal = this.paramList.dirName;
            this.uploadfile.folderListId = this.paramList.dirId;
          }
        }
      })
    },
    //获取标签列表
    getTagsList() {
      getTags().then(res => {
        if (res.data.msgNo == 1) {
          this.uploadfile.tagList = res.data.payload;
          this.uploadfile.tagListVal = '';
          this.uploadfile.tagListId = 0
        }
      })
    },
    //下拉选项变化
    changeModalselect(key, id) {
      this.uploadfile[key] = id;
    },
    //截取文件后缀名
    getFileType(filename) {
      let type = filename.substring(filename.lastIndexOf("."), filename.length);
      let unsupportType = ['.3gpp', '.ac3', '.mp2', '.mp3', '.mp4', '.mpeg', '.mpg', '.wma', '.aac'];
      if (unsupportType.indexOf(type) == -1) {
        return true
      } else {
        return false
      }
    },
    //截取文件后缀名
    getFileTypeString(filename) {
      filename = filename.toString();
      let type = filename.substring(filename.lastIndexOf(".") + 1, filename.length);
      return type;
    },
    //选择文件
    /*fileListChange(e) {
      Array.from(e.target.files).forEach(item => {
        console.log(item);
        item.typeCheck = this.getFileType(item.name);
        this.uploadfile.fileList.push(item);
      })
      e.target.value = "";
    },*/
    fileListChange(file, filelist) {
      //console.log(file);
      //console.log(filelist);
      this.uploadfile.fileList = [];
      filelist.forEach(item => {
        let type = getFileTypeMutil(item.name);
        item.typeCheck = this.getFileType(item.name);
        if (type == "") {
          dialogConfirm('info', '不支持该类型文件上传');
        } else {
          this.uploadfile.fileList.push(item);
        }
      })
      /*Array.from(e.target.files).forEach(item => {
        item.typeCheck = this.getFileType(item.name);
        this.uploadfile.fileList.push(item);
      })
      e.target.value = "";*/
    },
    //上传文件
    uploadFileFun() {
      if (this.fileType.filetypeList.length != 1) {
        if (this.ammend == 0 && this.level == 0 && (this.projectListVal == '' || this.projectListVal == -1)) {
          dialogConfirm('info', '您还没有选择' + this.typename);
        } else {
          this.fileuploadDialog = true;
          //this.getModalProjectList();
          this.getTagsList();
        }
      } else {
        dialogConfirm('info', '您还没有' + this.typename + '，请先创建' + this.typename);
      }

    },
    //删除文件
    deleteFile(index) {
      this.uploadfile.fileList.splice(index, 1);
    },
    //弹窗关闭确认
    dialogClose() {
      if (this.uploadfile.fileList.length > 0) {
        dialogConfirmHasbtn('确定不提交并关闭吗？').then(() => {
          this.uploadfile = {
            projectList: '',
            projectVal: '',
            projectId: '',
            folderList: '',
            folderListVal: '',
            folderListId: '',
            fileList: [],
            tagList: '',
            tagListVal: '',
            tagListId: '',
          }
          this.fileuploadDialog = false;
        }).catch(() => {});
      } else {
        this.fileuploadDialog = false;
      }
    },
    //弹窗提交按钮
    submitClick() {
      if (this.submitOk) {
        let param = {
          type: this.type,
          companyId: this.projectListValId,
          dirId: this.paramList.dirId,
          tagId: this.uploadfile.tagListId
        };
        if (this.type == 1) {
          param.tagId = this.uploadfile.tagListId;
        } else {
          param.tagId = ''
        }
        let fl = [];
        this.uploadfile.fileList.forEach(item => {
          fl.push(item.raw);
        })
        archiveUploadFile(param, fl).then(res => {
          //console.log(res);
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '文件上传成功');

          } else {
            dialogConfirm('info', res.data.message);
          }
          this.fileuploadDialog = false;
          this.uploadfile.fileList = [];
          this.initData();
        })
      } else {
        if (this.projectIdError || this.folderIdError || this.tagListError || this.uploadfile.fileList.length == 0) {
          dialogConfirm('info', '您还有必填项没有填写～')
        } else {
          dialogConfirm('info', '文件格式不正确')
        }

      }
    }

  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
@import '../../style/folder.scss';

</style>



